<template>
  <q-layout view="hHh lpR fFf">
    <q-page-container>
      <div class="q-pa-md">
        <!-- Barra de búsqueda -->
        <div class="searchDiv row items-center q-mb-md">
          <q-input
            class="searchInput col-grow"
            v-model="state.blurry"
            placeholder="Ingrese nombre o apellido del cliente"
            clearable
            outlined
            @keyup.enter="getClientesListFun"
          ></q-input>
          <!-- Botón con hover -->
          <q-button   
            color="primary" 
            outline 
            class="q-ml-sm custom-button" 
            type="primary" 
            @click="getClientesListFun"
          >
            Consulta
          </q-button>
        </div>

        <!-- Tabla de datos -->
        <q-table
          flat
          bordered
          ref="tableRef"
          title="Clientes"
          :rows="state.tableData"
          :columns="columns"
          row-key="id"
          :pagination="pagination"
          :loading="loading"
          :filter="filter"
          binary-state-sort
          :rows-per-page-options="[]" 
          @request="onRequest"
        >
          <template v-slot:top-right>
            <q-input
              borderless
              dense
              debounce="300"
              v-model="state.blurry"
              placeholder="Buscar"
              @keyup.enter="getClientesListFun"
            >
              <template v-slot:append>
                <q-icon name="search" @click="getClientesListFun " />
              </template>
            </q-input>
          </template>
        </q-table>

        <!-- Paginación -->
        <q-pagination
          v-model="pagination.page"
          :max="Math.ceil(state.total / state.size)"
          @update:model-value="getClientesListFun"
        />
      </div>
    </q-page-container>
  </q-layout>
</template>

<script setup>
import { ref, reactive, onMounted } from 'vue';
import { getClientesList } from '../../api/clientes/clientes';
import { errorMsg } from '../../utils/message';

// Estado reactivo
const state = reactive({
  blurry: '',
  tableData: [],
  current: 1,
  size: 7, // Tamaño fijo de página
  total: 0,
});

const columns = [
  { name: 'compania', required: true, label: 'Compañía', align: 'left', field: 'compania', sortable: true },
  { name: 'apellidos', required: true, label: 'Apellidos', align: 'left', field: 'apellidos', sortable: true },
  { name: 'nombre', required: true, label: 'Nombre', align: 'left', field: 'nombre', sortable: true },
];

const loading = ref(false);

// Configuración inicial de la paginación
const pagination = ref({
  sortBy: 'nombre',
  descending: false,
  page: 1,
  rowsPerPage: state.size, // Tamaño fijo de la página
});

// Obtener lista de clientes
const getClientesListFun = () => {
  loading.value = true;

  const params = {
    blurry: state.blurry,
    size: state.size,
    currentPage: pagination.value.page,
  };

  getClientesList(params)
    .then((res) => {
      loading.value = false;
      if (res.success) {
        state.tableData = res.data.records;
        state.total = res.data.total;
      } else {
        errorMsg(res.msg);
      }
    })
    .catch(() => {
      loading.value = false;
    });
};

// Manejar eventos de solicitud
const onRequest = (props) => {
  const { page, sortBy, descending } = props.pagination;

  pagination.value.sortBy = sortBy || '';
  pagination.value.descending = descending;
  pagination.value.page = page;

  state.current = page; // Actualizar el estado
  getClientesListFun();
};

// Cargar datos iniciales
onMounted(() => {
  getClientesListFun();
});
</script>

<style scoped>
.custom-button {
  border: 2px solid #1976D2;
  color: white;
  background-color: #1976D2;
  border-radius: 4px;
  padding: 10px 20px;
  transition: all 0.3s ease; /* Efecto de transición suave */
}

.custom-button:hover {
  background-color: #1565C0; /* Fondo más oscuro */
  border-color: #1565C0; /* Borde más oscuro */
  color: white; /* Texto blanco */
  cursor: pointer; /* Indicador de clic */
}
</style>
